import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';
import { Text, Localizer } from 'preact-i18n';

import PubSub, { topics } from '../../lib/pubsub';

import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

@observer
class YrWeather extends Component {
    constructor(props) {
        super(props);
        this.state = {};
		this.inputRef = {};
    }

    toggleyrWeather = () => {
        const { showWeather } = this.state;
        this.setState({
            showWeather: !showWeather,
        });
    }

    async loadWeather() {
		const { userStore } = this.props.stores;
		this.setState({ isLoading: true });
        const { user } = userStore;
		if (user.currentLocationInfo && user.currentLocationInfo.coords) {
			const { latitude: lat, longitude: lon, altitude } = user.currentLocationInfo.coords;
			userStore.getWeatherUser({ lat, lon, altitude });
            userStore.formatWeather();
		}
    }

	componentDidMount() {
        this.loadWeather();
	}

    render() {
        const { showWeather } = this.state;
        const { place } = this.props;
        const { userStore } = this.props.stores;
        const { yrWeather, yrWeatherKeys, yrWeatherFormatted, user } = userStore;
        const { currentLocationGeocode } = user;

        if (!yrWeather || !place) {
            return '';
        }
        if (showWeather) {
            return (
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-4'>
                    {currentLocationGeocode && currentLocationGeocode.formatted_address && <h5>{currentLocationGeocode.formatted_address}</h5>}
                    <div class='table-responsive'
                        onTouchstart={mu.captureEvents}
                        onTouchend={mu.captureEvents}
                        onTouchmove={mu.captureEvents}
                        onScroll={mu.captureEvents}
                    >
                        <table class='table table-striped table-sm'>
                            <thead>
                                <tr>
                                    <th class='text-center'>&nbsp;</th>
                                    <th class='text-center'><Text id='weather.night'>Night</Text></th>
                                    <th class='text-center'><Text id='weather.morning'>Morning</Text></th>
                                    <th class='text-center'><Text id='weather.afternoon'>Afternoon</Text></th>
                                    <th class='text-center'><Text id='weather.evening'>Evening</Text></th>
                                    <th class='text-center'><Text id='weather.temp'>Temp</Text></th>
                                    <th class='text-center'><Text id='weather.rainfall'>Rainfall</Text></th>
                                    <th class='text-center'><Text id='weather.wind'>Wind</Text></th>
                                </tr>
                            </thead>
                            <tbody>
                                {yrWeatherKeys && yrWeatherKeys.map(date => <>
                                    <tr>
                                        <td class='text-center'><nobr>{date}</nobr></td>
                                        <td class='text-center'>
                                            <nobr>
                                                {util.getNestedValue(yrWeatherFormatted[date].night, 'next_6_hours.summary.symbol_code') && <img style='width: 40px;' src={`./assets/svg/${util.getNestedValue(yrWeatherFormatted[date].night, 'next_6_hours.summary.symbol_code')}.svg`} />}
                                            </nobr>
                                        </td>
                                        <td class='text-center'>
                                            <nobr>
                                                {util.getNestedValue(yrWeatherFormatted[date].morning, 'next_6_hours.summary.symbol_code') && <img style='width: 40px;' src={`./assets/svg/${util.getNestedValue(yrWeatherFormatted[date].morning, 'next_6_hours.summary.symbol_code')}.svg`} />}
                                            </nobr>
                                        </td>
                                        <td class='text-center'>
                                            <nobr>
                                                {util.getNestedValue(yrWeatherFormatted[date].day, 'next_6_hours.summary.symbol_code') && <img style='width: 40px;' src={`./assets/svg/${util.getNestedValue(yrWeatherFormatted[date].day, 'next_6_hours.summary.symbol_code')}.svg`} />}
                                            </nobr>
                                        </td>
                                        <td class='text-center'>
                                            <nobr>
                                                {util.getNestedValue(yrWeatherFormatted[date].evening, 'next_6_hours.summary.symbol_code') && <img style='width: 40px;' src={`./assets/svg/${util.getNestedValue(yrWeatherFormatted[date].evening, 'next_6_hours.summary.symbol_code')}.svg`} />}
                                            </nobr>
                                        </td>
                                        <td class='text-center'>
                                            <nobr>
                                                <i class='fas fa-temperature-low text-muted ml-1 mr-2' />
                                                <span class={mu.getTemperatureColor(util.getNestedValue(yrWeatherFormatted[date], 'air_temperatures_min'))}>
                                                    <Temperature stores={this.props.stores} value={util.getNestedValue(yrWeatherFormatted[date], 'air_temperatures_min')} />
                                                </span> 
                                                <span class='text-muted font-weight-lighter mx-1'>-</span> 
                                                <span class={mu.getTemperatureColor(util.getNestedValue(yrWeatherFormatted[date], 'air_temperatures_max'))}>
                                                    <Temperature stores={this.props.stores} value={util.getNestedValue(yrWeatherFormatted[date], 'air_temperatures_max')} />
                                                </span>
                                            </nobr>
                                        </td>
                                        <td class='text-center'>
                                            <nobr>
                                                {util.format(util.getNestedValue(yrWeatherFormatted[date], 'precipitation_amount'), 1)} <span class='text-muted font-weight-lighter'>mm</span>
                                            </nobr>
                                        </td>
                                        <td class='text-center'>
                                            <nobr>
                                                <i class='fas fa-wind text-muted ml-1' /> <span class={mu.getWindSpeedColor(util.getNestedValue(yrWeatherFormatted[date], 'wind_speeds_min'))}>{util.format(util.getNestedValue(yrWeatherFormatted[date], 'wind_speeds_min'), 1)}</span> <span class='text-muted font-weight-lighter'>-</span> <span class={mu.getWindSpeedColor(util.getNestedValue(yrWeatherFormatted[date], 'wind_speeds_max'))}>{util.format(util.getNestedValue(yrWeatherFormatted[date], 'wind_speeds_max'), 1)}</span> <span class='text-muted font-weight-lighter'>m/s</span>
                                            </nobr>
                                        </td>
                                    </tr>
                                </>)}
                            </tbody>
                        </table>
                    </div>
                    <button type='button' class='btn btn-block btn-primary btn-sm' onClick={this.toggleyrWeather}>
                        <i class='fas fa-times-circle' /> <Text id='weather.close'>Close</Text>
                    </button>
                </div>
            );
        }
        return (
            <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
                {yrWeather && yrWeather.length > 0 && <div class='container-fluid px-0'>
                    <div class='row' onClick={this.toggleyrWeather}>
                        {yrWeather && yrWeather.length > 0 && yrWeather.slice(0, 1).map(w => (
                            <div class='col-12 text-right'>
                                <nobr>
                                    <small class='font-weight-light text-muted'>{util.isoTime(w.time)}</small>:
                                    <img src={`./assets/svg/${util.getNestedValue(w, 'next_1_hours.summary.symbol_code')}.svg`}
                                        class='ml-2'
                                        style='height: 20px;'
                                    />
                                    {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount') > 0 && <small class='ml-2'>
                                        {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount')}<span class='text-muted font-weight-lighter'>mm</span>
                                    </small>}
                                    <small class='ml-2'>
                                        <i class='fas fa-temperature-low text-muted' /> <Temperature stores={this.props.stores} value={util.getNestedValue(w, 'instant.details.air_temperature')} />
                                    </small> 
                                    <small class='ml-2'>
                                        <i class='fas fa-wind text-muted' /> {util.getNestedValue(w, 'instant.details.wind_speed')}<span class='text-muted font-weight-lighter'>m/s</span> <span class='text-muted font-weight-lighter'>{mu.windDirection(util.getNestedValue(w, 'instant.details.wind_from_direction'))}</span>
                                    </small> 
                                </nobr><br />
                            </div>
                        ))}
                    </div>
                </div>}
            </div>
        );
    }
}

export default YrWeather;
